<template>


        <div :style="{height: boxStyle['height']?boxStyle['height']:'1080px',width: boxStyle['width']?boxStyle['width']:'100%',background: boxStyle['background']?boxStyle['background']:'rgba(255,255,255,1)'}" >
          <div id='laylebox'>
           <el-button  class="transition-box"  type="primary"  plain @click="buclick(but)" v-for="(but,index) in data" v-bind:key="index" v-bind:autofocus="index==0" :style="{margin: buttonStyle['margin']?buttonStyle['margin']:'25px',height: buttonStyle['height']?buttonStyle['height']:'60px',width: buttonStyle['width']?buttonStyle['width']:'90px',fontSize: buttonStyle['fontSize']?buttonStyle['fontSize']:'30px'}">{{but}}</el-button>
        </div></div>
     




  
</template>

<script>
if(!data['buttonStyle']){
  data['buttonStyle']={height:'50px',width:'80px',fontSize:'25px',margin:'12.5px'}
}

if(!data['boxStyle']){
  data['boxStyle']={height:'500px',width:'100%',background:'black'}
}


export default {
  name: 'HelloWorld',
  data () {
    return data
  },
  methods: {
    buclick:  (mes)=> {
      ws.send(mes)
    }

  },mounted: function () {
            //组件挂载之后，点击第一个按钮
            this.$el.children[0].click()
           
            
        }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
head,body{
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#laylebox{
  position: relative;
    top: 50%;
    margin: 0 auto;
    width: 100%;
    transform: translateY(-50%);
}





</style>



